<template>
  <div>
    <div class="clearfix top-filter">
      <el-form ref="filterItem" :model="query" inline>
        <el-form-item prop="billNumber">
          <el-input
            v-model="query.billNumber"
            filterable
            clearable
            placeholder="退库单号"
            style="width: 178px;"
            class="filter-item"
          />
        </el-form-item>
        <el-form-item prop="sourceType">
          <el-select v-model="query.sourceType" filterable clearable placeholder="来源类型" style="width: 178px;" class="filter-item">
            <el-option
              v-for="(item,index) in dict['wms_out_warehouse_bill_type']"
              :key="index"
              :label="item.label"
              :value="item.value"
            />
          </el-select>
        </el-form-item>
        <el-form-item prop="sourceBillNumber">
          <el-input
            v-model="query.sourceBillNumber"
            filterable
            clearable
            placeholder="来源单号"
            style="width: 178px;"
            class="filter-item"
          />
        </el-form-item>
        <el-form-item prop="startTime">
          <el-date-picker
            v-model="query.startTime"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder="日期"
            style="width: 178px"
            class="filter-item"
          />
        </el-form-item>
        <el-form-item prop="status">
          <el-select
            v-model="query.status"
            filterable
            clearable
            placeholder="退库状态"
            style="width: 178px;"
            class="filter-item"
          >
            <el-option
              v-for="(item,index) in statusList"
              :key="index"
              :label="item.label"
              :value="item.value"
            />
          </el-select>
        </el-form-item>
        <el-button
          class="filter-item"
          type="success"
          icon="el-icon-search"
          @click="searchInit"
        >搜索</el-button>
        <el-button
          class="filter-item"
          type="warning"
          icon="el-icon-refresh-left"
          @click="resetForm('filterItem')"
        >重置</el-button>
        <el-button
          class="filter-item"
          type="primary"
          icon="el-icon-plus"
          @click="handleAdd"
        >新增</el-button>
      </el-form>
    </div>
    <el-row>
      <table-com
        ref="baseTableRef"
        :loading="loading"
        :selection="false"
        :table-column-sort="true"
        :form-ident="formIdent"
        :table-data="tableData"
        :column-list="columnList"
        :operation-width="300"
        :refresh-key="refreshKey"
        :expand-list="expandList"
        @rowClick="handleRowClick"
        @refresh="searchInit"
      >
        <template slot-scope="scope">
          <el-button type="primary" @click="getDetail(scope.row)">详情</el-button>
          <el-button :disabled="scope.row.status === 2" type="warning" @click="handleModify(scope.row)">编辑</el-button>
          <el-button :disabled="scope.row.status === 2" type="danger" @click="handleDelete(scope.row)">删除</el-button>
          <el-button type="primary" @click="handlePrint(scope.row)">打印</el-button>
        </template>
      </table-com>
    </el-row>
    <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" />
    <!-- 新增退库 -->
    <return-warehouse-order-dialog
      v-model="dialogVisible"
      :selection="[]"
      :multiple="false"
      @confirm="onSelectConfirm"
    />
  </div>
</template>
<script>
import { getListApi, getPullListItemApi, getDeleteApi, getAddApi, printApi } from '@/apis/modules/out-bound/stock-return'
import paginationPage from '@/mixins/paginationPage'
import Pagination from '@/components/Pagination'
import ReturnWarehouseOrderDialog from '@/components/ModalSelect/ReturnWarehouseOrder'
import { requestWrapper } from '@/utils/utils'
import tableHeadSet from '@/mixins/tableHeadSet'
import { mapGetters } from 'vuex'

export const statusChartMap = {
  1: '未完成',
  2: '已完成'
}

export default {
  components: {
    Pagination,
    ReturnWarehouseOrderDialog
  },
  mixins: [paginationPage, tableHeadSet],
  dicts: ['wms_out_warehouse_bill_type'],
  data() {
    return {
      query: {
        billNumber: '',
        sourceBillNumber: '',
        sourceType: '',
        startTime: '',
        status: ''
      },
      dialogForm: {},
      custumerPullList: [],
      title: this.$route.meta.title,
      loading: false,
      tableData: [],
      statusList: [
        { label: '未完成', value: 1 },
        { label: '已完成', value: 2 }
      ],
      columnList: [
        {
          module: 'status',
          prop: 'sourceType_dictText',
          label: '来源类型',
          align: 'center',
          // width: 120,
          // minWidth: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: (prop) => {
            return prop
          },
          headerAlign: 'center',
          statusFormatter: (scope) => {
            const typeMap = {
              '1': 'success',
              '2': 'primary',
              '3': 'warning',
              '4': 'info'
            }
            return scope.sourceType ? typeMap[scope.sourceType] : ''
          }
        },
        {
          module: 'word',
          prop: 'billNumber',
          label: '退库单号',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center',
          formatter: null

        },
        {
          module: 'word',
          prop: 'sourceBillNumber',
          label: '来源单号',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center',
          formatter: null

        },
        {
          module: 'word',
          prop: 'stockOutNumber',
          label: '出库单号',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'date',
          label: '出库日期',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'status',
          prop: 'status',
          label: '状态',
          align: 'center',
          minWidth: '86',
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center',
          formatter: (status) => {
            return statusChartMap[status]
          },
          statusFormatter: (row) => {
            const typeMap = {
              '1': 'success',
              '2': 'warning'
            }
            return row.status ? typeMap[row.status] : ''
          }
        }

      ],
      columnSets: [
        {
          module: 'status',
          prop: 'status',
          formatter: (status) => {
            return statusChartMap[status]
          },
          statusFormatter: (row) => {
            const typeMap = {
              '1': 'success',
              '2': 'warning'
            }
            return row.status ? typeMap[row.status] : ''
          }
        }
      ],
      dialogType: 'add',
      dialogVisible: false,
      formThead: [
        { label: '单号', prop: 'billNumber' },
        { label: '日期', prop: 'date' },
        { label: '作业合同/WBS号', prop: 'contractWbsNumber' }
      ]
    }
  },
  computed: {
    dialogTitle() {
      if (this.dialogType === 'add') {
        return `新增${this.title}`
      } else {
        return `编辑${this.title}`
      }
    },
    ...mapGetters('app', {
      navbarWarehouseCheckedList: 'navbarWarehouseCheckedList'
    })
  },
  watch: {
    navbarWarehouseCheckedList: {
      handler() {
        this.init()
      },
      deep: true
    }
  },
  mounted() {
    this.init()
    this.getcustumerPullList()
  },
  methods: {
    init() {
      this.loading = true
      return new Promise((resolve, reject) => {
        getListApi({
          page: {
            ...this.pageInfo
          },
          ...this.query,
          warehouseIds: this.navbarWarehouseCheckedList.map(item => item.id)
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { formIdent, formHeadSets, pageData } = data
            this.formIdent = formIdent
            this.columnList = this.initTableHeadMixins(formHeadSets)
            const { list, ...pageInfo } = pageData
            this.initPage(pageInfo)
            this.tableData = list
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    handleRowClick({ row, column, cell, event }) {
      console.log(row)
      if (column.label === '操作') {
        return false
      }
      this.rightObj = { ...row }
    },
    // 新增
    handleAdd() {
      // this.dialogType = 'add'
      // this.dialogForm = {}
      // this.dialogVisible = true
      this.$router.push({
        name: 'WmsOutBoundAddStockReturnInfo'
      })
    },
    // 下拉数据
    getcustumerPullList() {
      return new Promise((resolve, reject) => {
        getPullListItemApi({}).then(res => {
          const { code, data } = res
          if (!code) {
            this.custumerPullList = data || []
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    // 修改
    handleModify(item) {
      this.$router.push({
        name: 'WmsOutBoundStockEdit',
        params: {
          stockOutNumber: item.stockOutNumber, // 出库单号
          billNumber: item.billNumber, // 退库单号
          childrenType: 'edit',
          date: item.date
        }
      })
    },
    // 详情
    getDetail(item) {
      this.$router.push({
        name: 'WmsOutBoundStockDetail',
        params: {
          stockOutNumber: item.stockOutNumber, // 出库单号
          billNumber: item.billNumber, // 退库单号
          childrenType: 'detail',
          date: item.date
        }
      })
    },
    // 选择退库单
    async onSelectConfirm([item]) {
      const res = await requestWrapper(getAddApi(item))
      if (!res.success) return
      this.init()
    },
    handleDelete(item) {
      this.$confirm(`此操作将永久删除该${this.title}, 是否继续?`, '提示', {
        confirmButtonText: '确 定',
        cancelButtonText: '取 消',
        cancelButtonClass: 'btn-custom-cancel',
        type: 'warning',
        lockScroll: false
      }).then(() => {
        if (!item.id) return
        getDeleteApi({
          id: item.id,
          dataSources: item.dataSources
        }).then(res => {
          const { code } = res
          if (!code) {
            this.$message({
              message: '删除成功',
              type: 'success'
            })
            this.init()
          }
        })
      }).catch(() => {
        this.$message({
          message: '已取消删除',
          type: 'info'
        })
      })
    },
    // Tip 打印
    handlePrint(item) {
      const { billNumber } = item
      const params = { billNumber }
      return new Promise((resolve, reject) => {
        printApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            console.log('data', data)
            window.open(process.env.VUE_APP_BASE_API_WMS + data)
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.searchInit()
    }
  }
}
</script>

